const ADD_MESSAGE = 'ADD-MESSAGE';

let initialState = {
  dialogs: [
    {
      id: 1,
      name: 'Alexey',
      imgSrc:
        'data:image/jpeg;base64,'
    },
    {
      id: 2,
      name: 'Alina',
      imgSrc:
        'data:image/jpeg;base64,'
    },
    {
      id: 3,
      name: 'Dima',
      imgSrc:
        'data:image/jpeg;base64,'
    },
    {
      id: 4,
      name: 'Sasha',
      imgSrc:
        'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR-AyJmBjoXsCq5N2ifSGNmULprS6Sf-Z9dng&usqp=CAU'
    },
    {
      id: 5,
      name: 'Andrey',
      imgSrc:
        'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTeKppZIP778EgdIsmSe8qVujsYk-YaKmuwvw&usqp=CAU'
    },
    {
      id: 6,
      name: 'Sveta',
      imgSrc:
        'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQTbt_6RMAYEh1MXQ8HICrK5qkDLzoz8qD5gw&usqp=CAU'
    }
  ],
  messages: [
    { id: 1, message: 'Hi' },
    { id: 2, message: 'How is yor day?!' },
    { id: 3, message: 'I am fine.!' },
    { id: 4, message: 'What?' },
    { id: 5, message: 'Nice!' },
    { id: 6, message: 'Ok.' }
  ]
};

const dialogsReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_MESSAGE:
      let body = action.newMessage;
      return {
        ...state,
        messages: [
          ...state.messages,
          { id: state.messages.length + 1, message: body }
        ]
      };

    default:
      return state;
  }
};

export const addMessageActionCreator = (newMessage) => ({
  type: ADD_MESSAGE,
  newMessage
});
export default dialogsReducer;
